<template lang="html">
    <div ref="editor" class="editorTwo" @input="outputContent" ></div>
</template>

<script>
import _ from 'lodash'
import E from 'wangeditor'

export default {
    name:'EditorTwo',
    props:{
      inputContent:{
          type:String,
          default:''
      } 
    },
    data() {
        return {
            editor:null,
            editorContent: '',    
            menus :[
                'head',  // 标题
                'bold',  // 粗体
                'fontSize',  // 字号
                'fontName',  // 字体
                'italic',  // 斜体
                'underline',  // 下划线
                'strikeThrough',  // 删除线
                'foreColor',  // 文字颜色
                'backColor',  // 背景颜色
                'justify',  // 对齐方式
                'quote',  // 引用
                'undo',  // 撤销
                'redo',  // 重复
            ]
        }
    },
    mounted() {
        this.createEditor()
    },
    watch:{
        inputContent(curVal){
           this.editor.txt.html(curVal);
        }
    },
    methods: {
        createEditor(){
            const self = this
            const editor = new E(self.$refs.editor)
            
            self.editor=editor;
            editor.customConfig.menus=self.menus
            editor.customConfig.zIndex = 0;
            editor.customConfig.onchange = (html) => {
                self.outputContent(html)
            }
            editor.create();

        },
        outputContent:_.debounce(function(content){
             this.$emit('input', content)
        }, 500)
    }
}
</script>
<style lang="scss">
   .editorTwo{
       text-align:left;
       .w-e-text-container{
          overflow: auto;
          height: auto !important;
          min-height:40px;
          max-height:300px;
       }
       .w-e-toolbar .w-e-menu i{
            color: #333;
            font-size: 16px;
        }
   }
</style>


