<template>
   <div class="center">
    <div class="center_content">
        <div class="center_breadcrumb">
           <crumb :data="pageTitle"></crumb>
        </div>
         <div class="question-main" :class="{'question-main--min': isCollapse}">
             <!-- 题型 -->
            <div class="question-mian_type" :class="{'ceiling': isTop}">
               <div v-for="(vo, index) in questionTtype" :key="index" class="type-module">
                 <p>{{vo.title}}</p>
                 <ul>
                   <li @click="topicSelection(item.data)" v-for="(item, num) in vo.children" :key="num">
                      <SvgIcon :icon-class="item.icon" size="15px"></SvgIcon>
                      <label>{{item.name}}</label>
                   </li>
                 </ul>
               </div>
            </div>
            <!-- 题内容 -->
            <div class="question-main_wrap">
               <div class="q_content  q_content-head">
                  <div class="q_content_title">
                     <!-- <edit-box v-model="form.formTitle" fontSize="18px"></edit-box> -->
                     <div style="margin-bottom:20px">
                        <editor-two v-model="form.formTitle"  :input-content="introduction"> </editor-two>
                     </div>       
                     <edit-box v-model="form.remarks"></edit-box>               
                  </div>
               </div>
               <!-- 主体逻辑区域 -->
               <div v-if="list.length === 0" class="q_null_tip"><SvgIcon icon-class="icondianjitixing" size="20px"></SvgIcon><label>点击题型</label></div>
               <draggable  v-model="list" @end="draggableEnd" v-bind="{filter:'.undraggable', animation:150, ghostClass:'ghostClass'}">
               <template v-for="(vo, index) in list">
                   <div :key="index" @click="selectContent(index, vo)" class="q_content" :class="{'q_content-active': activeIndex === index && vo.type !=='splitLine'}" :id="`q_item${index}`">
                        <div class="q_content_head"> 
                           <!-- 分割线 -->
                            <div v-if="vo.type === 'splitLine'" class="line"></div>
                           <!-- 备注说明 -->
                           <label v-if="vo.type === 'baseRemark'" class="q_sort"> <SvgIcon icon-class="iconyinhao" size="20px"></SvgIcon></label>  
                           <!-- 其他所有的  -->
                           <label v-if="vo.type !== 'splitLine' && vo.type !== 'baseRemark'" class="q_sort">{{vo.sort}}</label>     
                           <edit-box v-if="vo.type !== 'splitLine'" v-model="vo.title" :isEdit="isEditFn(vo.type)"></edit-box>    
                           <div class="q_tooltip" :class="{'q_tooltip-active': activeIndex === index}">
                              <el-tooltip class="q_tips" effect="dark" content="长按拖动题目" placement="top">
                                 <label><SvgIcon icon-class="iconchangantuodong" size="20px"></SvgIcon> </label>                                                            
                              </el-tooltip>
                              <el-tooltip class="q_tips" effect="dark" content="逻辑设置" placement="top">
                                 <label  @click.stop="setRow(index, vo)"><SvgIcon v-if="vo.type !=='splitLine'" icon-class="icontimushezhi" size="20px"></SvgIcon></label>
                              </el-tooltip>
                              <el-tooltip  v-if="vo.type !=='splitLine'" class="q_tips" effect="dark" content="复制题目" placement="top">
                                 <label  @click.stop="copyRow(index, vo)"><SvgIcon icon-class="iconfuzhi" size="20px"></SvgIcon></label>
                              </el-tooltip>
                              <el-tooltip class="q_tips" effect="dark" content="删除题目" placement="top">
                                 <label @click.stop="deleteRow(index)"><SvgIcon icon-class="iconshanchu1" size="20px"></SvgIcon></label>
                              </el-tooltip>                     
                           </div>
                        </div>
                        <!-- 单选 多选 性别-->
                         <draggable tag="ul" v-if="vo.type ==='gradeRadio' || vo.type ==='gradeCheckbox' || vo.type ==='sex'" class="q_content_item" v-model="vo.item"  v-bind="{filter:'.undraggable',  animation:150,  ghostClass:'ghostClass'}">
                           <li v-for="(single, num) in vo.items" :key="num">
                              <el-tooltip class="q_tips" effect="dark" content="长按拖动选项" placement="top">
                                 <label class="first-dark"><SvgIcon icon-class="iconshuangpaiyuandian"  size="20px"></SvgIcon></label>                                                            
                              </el-tooltip>
                              <i v-if="vo.type ==='gradeRadio' || vo.type ==='sex'" class="icon_radio"></i>
                              <i v-if="vo.type ==='gradeCheckbox'" class="icon_checkbox"></i>
                              <edit-box v-model="single.text"  :isEdit="isEditFn(vo.type)"></edit-box>
                              <el-tooltip class="q_tips-end" effect="dark" content="删除" placement="top">
                                 <label @click.stop="deleteItem(index, num)" class="last-btn"><SvgIcon icon-class="iconqudiao" size="20px"></SvgIcon></label>    
                           </el-tooltip>                     
                           </li>
                        </draggable>
                        <!-- 填空题 简答题 个人信息-->
                        <div v-if="vo.type ==='baseText' || vo.type ==='baseTextArea' || vo.type ==='name' || vo.type ==='phone' || vo.type ==='email' || vo.type ==='academy' || vo.type ==='specialty' || vo.type ==='industry' || vo.type ==='profession'" class="q_blank_wrap"></div>
                        <!-- 量表题 -->
                        <ul v-if="vo.type ==='baseGauge'" class="q_score-item">
                           <li v-for="(start, n) in vo.itemNum" :key="n"><span class="new_radio" :class="{'new_radio_checked': n === 0}"></span><label>{{start}}</label> </li>                                                                          
                        </ul> 
                        <div v-if="vo.type ==='baseGauge'" class="q_scale_title">
                           <span>{{vo.startDesc}}</span> <span>{{vo.endDesc}}</span>
                        </div>
                        <!-- 矩阵题 -->
                        <ul v-if="vo.type ==='matrixGauge'" class="q_table"> 
                           <li class="q_table_head">
                              <div class="table_head_left"> </div>        
                              <div class="table_head_scale">
                                 <ul><li class="scale_lable">{{vo.startDesc}}</li><li class="scale_lable">{{vo.endDesc}}</li></ul>
                                 <ul>
                                    <li v-for="(start, n) in vo.itemNum" :key="n" class="scale_num">{{start}}</li>
                                 </ul>
                              </div>
                           </li>  
                           <li v-for="(row, rowIndex) in vo.rowDesc" :key="rowIndex" class="q_table_body">
                              <div class="q_table_body_title">
                                 <edit-box v-model="row.text"></edit-box> 
                                 <label @click.stop="tableHandle(index, rowIndex)"><SvgIcon icon-class="icongengduo" size="20px"></SvgIcon></label>
                                 <ul v-show="tableActive === `${index}_${rowIndex}`" class="operate-tip">
                                    <li @click.stop="tableRowUp(index, rowIndex, row)">上移选项</li>
                                    <li @click.stop="tableRowDown(index, rowIndex, row)">下移选项</li>
                                    <li @click.stop="tableRowDelete(index, rowIndex)">删除</li>
                                 </ul>
                              </div>
                              <div class="q_table_body__wrap">
                                 <span v-for="(start, n) in vo.itemNum" :key="n" class="new_radio" :class="{'new_radio_checked': n === 0}"></span>
                              </div>
                           </li>                                                                         
                        </ul>                    
                        <!-- 上传 -->
                         <div v-if="vo.type ==='uploadFile'" class="q_upFile">
                           <SvgIcon icon-class="iconshangchuan" size="18px"></SvgIcon>
                           <span>选择文件（30M以内）</span>
                        </div>
                        <!-- 添加项 -->
                        <div v-if="vo.type ==='gradeRadio' || vo.type ==='gradeCheckbox' || vo.type ==='matrixGauge'" class="q_option_operate">
                           <span @click.stop="addItem(vo.type, index)" class="btn_text_icon"><SvgIcon icon-class="icontianjia" size="15px"></SvgIcon><label>添加单个选项</label></span>
                        </div>               
                     </div>
               </template>
               </draggable>
               <!-- 主体尾部 -->
                <div class="q_content">
                  <div class="q_content_title">
                     <edit-box v-model="form.endWord"></edit-box>
                     <div class="footer-btn">
                         <el-button @click="submit('save')" class="q-button">保存</el-button>
                        <el-button v-if="entryType === 'new'" @click="submit('issue')" class="q-button" type="primary">发布</el-button>  
                     </div>                
                  </div>
               </div>              
            </div>
            <!-- 设置 -->
            <div class="question-main_setting"  :class="{'setting-left': isSetting , 'setting-right': !isSetting , 'ceiling': isTop}">
              <div class="q_title">Q{{settingData.sortNum}}.题目设置</div>
              <div class="q_block_question">
                 <div class="q_block_question_head">整题设置</div>
                 <div class="q_block_question_body">
                    <span class="hd_txt">此题必填</span>
                    <el-switch @change="settingChange"  v-model="settingData.required"></el-switch>                            
                 </div>
                  <div v-if="settingData.isGauge" class="q_block_question_body border-top border-bom">
                    <span class="hd_txt">极值标签</span>
                    <el-select @change="settingChange" v-model="extremum" class="q_select" placeholder="">
                     <el-option
                        v-for="item in extremumOptions"
                        :key="item.id"
                        :label="item.label"
                        :value="item.id">
                     </el-option>
                  </el-select>                     
                 </div>
                  <div v-if="settingData.isGauge" class="q_block_question_body">
                    <span class="hd_txt">量级设置</span>
                    <el-select @change="settingChange" v-model="order" class="q_select" placeholder="">
                     <el-option
                        v-for="item in orderOptions"
                        :key="item.id"
                        :label="item.label"
                        :value="item.id">
                     </el-option>
                   </el-select>                            
                 </div>
                 <div v-if="settingData.isScore" class="q_block_question_body border-top border-bom">
                    <span class="hd_txt">选项分值设置</span>          
                 </div>
                 <ul v-if="settingData.isScore" class="q_score">
                    <li><label>各选项分值</label></li>
                    <li v-for="(vo, n) in settingData.scoreArr" :key="n"><label>选项{{ n+1 }}</label><label class="q_tip">分</label><input @change="settingChange" v-model="vo.scores" class="q_input" type="text"/></li>
                </ul> 
                <div v-if="entryType === 'graduate' || entryType === 'course'" class="q_block_question_body border-top border-bom" style="clear: both">
                    <span class="hd_txt">{{entryType === 'graduate' ? '关联毕业要求' : '关联课程'}}</span>          
                 </div>
                 <div v-if="entryType === 'graduate' || entryType === 'course'" class="graduate">
                     <p class="graduate-title">{{entryType === 'graduate' ? '关联毕业要求及指标点' : '关联课程及课程目标'}}</p>
                     <el-select v-model="settingData.graduateValue" @change="getPoints" :placeholder="graduateTitle">
                        <el-option
                           v-for="item in graduateOptions"
                           :key="item.id"
                           :label="item.name"
                           :value="item.id">
                        </el-option>
                     </el-select>
                     <el-select v-model="settingData.pointsValue" @change="settingChange" :placeholder="pointsTitle">
                        <el-option
                           v-for="item in pointsOptions"
                           :key="item.id"
                           :label="item.name"
                           :value="item.id">
                        </el-option>
                     </el-select>
                 </div>
              </div>
            </div>
        </div>
    </div>
 </div>
</template>

<script>
import EditorTwo from '../components/EditorTwo'
import questionData  from '@/utils/questionnaire'
import draggable from 'vuedraggable'
import EditBox from '@/components/EditBox/index'
import Self from '@/api/self'

export default {
 name: 'EditQuestionnaire',
 components: {draggable, EditBox, EditorTwo},
 data() {
 return {
   pageTitle:['发起调查', '编辑问卷'],
   typeOption:['name', 'phone', 'email', 'sex', 'academy', 'specialty', 'industry', 'profession'],
   introduction:'',
   isCollapse:false,
   isTop:false,
   entryType:this.$route.query.entryType,
   courseName:this.$route.query.courseName,
   questionTtype:questionData.questionTtype,
   activeIndex:-1,
   tableActive:'0',
   form:{
      formId:this.$route.query.id || '' , 
      formTitle:'',
      remarks:'',
      endWord:'',
      data:''
   },
   list:[],
   isSetting:false,
   settingData:{
      sortNum:null,
      type:'',
      isScore:false,
      isGauge:false,
      required:true,
      scoreArr:[],
      graduateValue: '', //毕业要求 或者 课程
      pointsValue:'',  //指标点 或者 课程目标
   },
   extremum:'1',
   order:'5',
   extremumOptions:[{id:'1', label:'满意度'}, {id:'2', label:'认同度'}],
   orderOptions:[{id:'5', label:'5级'}, {id:'10', label:'10级'}],
   graduateOptions:[],
   pointsOptions:[],
   api:{
      queryQuestionAll:'/form/rest/FormQuestionBaseBusiness/queryQuestionAll/v1', //毕业要求 课程质量 普通问卷详情
      templateQueryQuestionAll:'/form/rest/FormTemplateQuestionBusiness/queryQuestionAll/v1', //模板问卷详情
      listByVersion: versionId => `/basic/claim/listByVersionId/${versionId}`, //毕业要求
      points:claimID => `/basic/point/Claim/${claimID}`, //指标点 
      courseGoal:courseId => `/basic/courseGoal/course/${courseId}`, //课程目标
      save:{
         graduate:'/form/rest/FormQuestionBaseBusiness/saveRequirementsMetQuestionAll/v1', //毕业要求问卷保存
         course:'/form/rest/FormQuestionBaseBusiness/saveCourseQualityQuestionAll/v1', //课程质量问卷保存
         new:'/form/rest/FormQuestionBaseBusiness/saveQuestionAll/v1',  //普通问卷保存
         template:'/form/rest/FormTemplateQuestionBusiness/saveQuestionAll/v1' , //模板问卷保存

      },
      saveTemplate:()=>`/form/rest/FormTemplateMainBaseBusiness/saveTemplate/v1`,//保存为普通问卷模板库
      saveCourseTemplate:()=>`/form/rest/FormTemplateMainBaseBusiness/saveCourseTemplate/v1` //保存为课程模板库
   }
 }
 },
 created(){
    console.log('origin：', this.entryType)
    this.initData()
    Self.$on('OpenSideBar', val => {
           this.isCollapse=val
        })
 },
 mounted(){
    const self=this
    window.addEventListener('scroll', function(e){
      if(e.target.scrollTop > 35){
           self.isTop=true
      }else{
          self.isTop=false
      }
    }, true)
 
 },
 computed:{
    graduateTitle(){
      return this.entryType === 'graduate' ? '请选择毕业要求' : '请选择课程'
    },
    pointsTitle(){
      return this.entryType === 'graduate' ? '请选择指标点' : '请选择课程目标'
    }
 },
 methods:{
    initData(){
      const postData={
         formId:this.form.formId,
         templateId:this.$route.query.oldId || undefined, //只针对于课程模板引用进来
         //formType 1.毕业要求，2.课程，3.普通
         formType: this.entryType === 'graduate' ? 1 : this.entryType === 'course' ? 2 : this.entryType === 'new' ?  3 : undefined
      }
      this.$post(this.entryType === 'template' ? this.api.templateQueryQuestionAll : this.api.queryQuestionAll, postData)
         .then(res => {      
            this.list=res.data.questions || []
            this.form.data=JSON.stringify(this.list)
            this.$nextTick(()=>{
               // this.form.formTitle=res.data.form.formName || '问卷标题，点击标题可编辑'
                this.introduction=res.data.form.formTitle || '问卷标题，点击标题可编辑'
                this.form.remarks=res.data.form.remarks || '添加编辑问卷说明'
                this.form.endWord=res.data.form.endWord || '可编辑结束语,例如：问答已完成，感谢您参与问卷'
            })
            this.sortShowIndex()
            if(this.entryType === 'graduate'){
                  this.graduation(res.data.form.ext1)
               }
            if(this.entryType === 'course'){
               //课程
               this.graduateOptions=[
                  {
                     id:res.data.form.ext3,
                     name:this.courseName
                  }
               ]
            }
         })
    },
    //毕业要求
   graduation(versionId){
      this.$get(this.api.listByVersion(versionId)).then(res =>{
         this.graduateOptions=res.data || []
      })
   },
   //指标点及课程目标
   getPoints(){
     this.list[this.activeIndex].propValue1=this.settingData.graduateValue
     const url = this.entryType === 'graduate' ?  this.api.points(this.settingData.graduateValue) : this.api.courseGoal(this.settingData.graduateValue)   
     this.$get(url).then(res =>{
          const self=this
         this.pointsOptions=res.data || []
         const isId=this.pointsOptions.every(item =>{
            return item.id !==self.settingData.pointsValue
         })

         if(isId) self.settingData.pointsValue =undefined
         if(this.entryType === 'graduate'){
            this.pointsOptions.unshift({
               name:'全部',
               id:''
            })
         }
      })
   },
   draggableEnd(e){
      setTimeout(()=>{
         this.sortShowIndex()
         this.activeIndex=e.newIndex
         this.settingPublic('content', e.newIndex, this.list[e.newIndex])
         this.sidebarMovement(this.list[e.newIndex].type)
         this.tableActive='0'
      },500)
      
   },
   //题型栏-选择题型
   topicSelection(data){
      const indexd= this.activeIndex === -1 ? 0 : this.activeIndex
      const newData=JSON.parse(JSON.stringify(data)) 
      //毕业要求及课程质量问卷关联数据  
      if(this.entryType === 'graduate' || this.entryType === 'course'){
          //毕业要求 或者 课程
           newData.propValue1=''
         //指标点 或者 课程目标
           newData.propValue2=''
      } 
      this.list.splice(indexd +1, 0, newData);  
      this.sortShowIndex()
     if(newData.type !=='splitLine')  this.activeIndex=this.list.length -1    
     if(newData.type ==='baseGauge' || newData.type ==='matrixGauge'){
         this.extremum='1'
         this.order='5'
     }
     this.sidebarMovement(newData.type) 
     this.settingPublic('side',indexd, newData) 
     this.tableActive='0'
     //滚动条滚动到指定元素
     this.$nextTick(()=>{
        document.getElementById(`q_item${indexd}`).scrollIntoView()
     })
     
   },
    selectContent(index, vo){
     this.activeIndex=index
     this.settingPublic('content', index, vo)
     this.sidebarMovement(vo.type)
     this.tableActive='0'
   },
   //所有题编号排序
   sortShowIndex(){
      let num=0
      this.list.forEach((_vo, _index) =>{
        if(_vo.type === 'splitLine' || _vo.type === 'baseRemark'){
             _vo.sort=null
        }else{ 
            num+=1
            _vo.sort=num
        }
      })
      this.$set(this.list)
   },
   //设置
   setRow(index, vo){
    this.sidebarMovement(vo.type)
   },
   //复制
   copyRow(index, vo){
    const item=JSON.parse(JSON.stringify(vo))
    item.id=""
    this.list.splice(index, 0, item)
    this.activeIndex=index + 1
    this.sortShowIndex()
    this.settingPublic('content', index + 1, vo)
    this.sidebarMovement(vo.type)
   
   },
   //删除
   deleteRow(index){
      this.$confirm('确定要删除吗？', '删除题目确认', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
        }).then(() => {
            this.list.splice(index, 1)
            this.activeIndex=this.list - 1
            this.isSetting=false
            this.sortShowIndex()
        }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            });
        });
   }, 
   //单个矩阵题操作栏显示
   tableHandle(index, rowIndex, row){ 
      this.tableActive = `${index}_${rowIndex}`
   },
   tableRowUp(index, rowIndex, row){
      if(rowIndex === 0) {
         this.$message('已经是第一个选项')
      }else{
        this.list[index].rowDesc=this.swapItems(this.list[index].rowDesc, rowIndex, rowIndex - 1)
      }
     this.tableActive='0'
   },
   tableRowDown(index, rowIndex){
      if(rowIndex === this.list[index].rowDesc.length -1) {
         this.$message('已经是最后一个选项')
      }else{
          this.list[index].rowDesc=this.swapItems(this.list[index].rowDesc, rowIndex, rowIndex + 1)
      }   
       this.tableActive='0'
   },
   //上移下移公共方法
   swapItems(arr, index1, index2){
   　　arr[index1] = arr.splice(index2,1,arr[index1])[0]
   　　return arr
   },
   tableRowDelete(index, rowIndex){
      if(this.list[index].rowDesc.length <=1){
         this.$message('请至少保留1个选项')
      }else{
         this.list[index].rowDesc.splice(rowIndex, 1)
      }
      this.tableActive='0'
     
   },
   //单个选项删除
   deleteItem(index, num){
      if( this.list[index].items.length <=1){
         this.$message('请至少保留1个选项')
         return
      }
      this.list[index].items.splice(num, 1)
   },
    //添加单个选项
   addItem(type, index){
      if(type === 'matrixGauge'){
          const obj= { text: `标题${this.list[index].rowDesc.length +1}`}
         this.list[index].rowDesc.push(obj)         
      }else{
         const obj= { text: `选项${this.list[index].items.length +1}` }
         this.list[index].items.push(obj)
      }
      
   },
    //设置栏-展示及隐藏
   sidebarMovement(type){
      if(type === 'baseRemark' || type === 'splitLine'){
          this.isSetting=false
          return
      }
      if(this.isSetting){
        this.isSetting=false
        setTimeout(()=>{
            this.isSetting=true
        },600)
     }else{
        this.isSetting=true
     } 
   },
   //设置栏-操作类目控制及赋值
   settingPublic(originType, index, vo){
      if(originType ==='side') this.activeIndex = this.list.length <= 1 ? index : index +1
       if(vo.type === 'baseRemark' || vo.type === 'splitLine'){
          this.isSetting=false
          return
      }   
      
     this.settingData.required=vo.required
     this.settingData.type=vo.type
     this.settingData.sortNum=vo.sort
     
    if(this.entryType === 'graduate' || this.entryType === 'course'){
         this.settingData.graduateValue= vo.propValue1
         this.settingData.pointsValue=vo.propValue2
         //有数据接口请求
         if(vo.propValue1){
            this.getPoints()
         }
    }
     if(vo.type === 'gradeRadio' || vo.type ==="gradeCheckbox"){
          this.settingData.isScore=true
          this.settingData.isGauge=false
          this.settingData.scoreArr=vo.items
     }else if(vo.type === 'baseGauge' || vo.type ==="matrixGauge"){
         this.extremum= vo.endDesc === '非常满意' ? '1' : '2'
         this.order=`${vo.itemNum}`
         this.settingData.isScore=false
         this.settingData.isGauge=true
     }else{
         this.settingData.isScore=false
         this.settingData.isGauge=false
     }
     
   },
   //设置栏-操作变化数据处理
   settingChange(){
      const reg = /^(0|[1-9][0-9]*)$/
      this.list[this.activeIndex].required=this.settingData.required
     if(this.settingData.type === 'gradeRadio' || this.settingData.type ==="gradeCheckbox"){
          this.list[this.activeIndex].items.forEach((vo, k) =>{
                let score= this.settingData.scoreArr[k].scores
                if(!reg.test(Number(score))){
                    //this.$message('请输入正整数')
                    this.settingData.scoreArr[k].scores='0'
                }
                  vo.scores=this.settingData.scoreArr[k].scores
          })
     }else if(this.settingData.type === 'baseGauge' || this.settingData.type ==="matrixGauge"){
            this.list[this.activeIndex].startDesc= this.extremum === '1' ? '非常不满意' : '非常不赞同'
            this.list[this.activeIndex].endDesc= this.extremum === '1' ? '非常满意' : '非常赞同'
            this.list[this.activeIndex].startIndex=1
            this.list[this.activeIndex].itemNum=this.order === '5' ? 5 : 10
     }

    if(this.entryType === 'graduate' || this.entryType === 'course'){
         this.list[this.activeIndex].propValue2=this.settingData.pointsValue
    }
     
   },
   submit(type){
      const arr=JSON.parse(JSON.stringify(this.list))
      let num=0
      arr.forEach(item =>{
         num+=1
         item.sort=num
      })
      this.form.data=JSON.stringify(arr)
      const url =this.api.save[this.entryType]
      this.$post(url, this.form)
         .then(res => {
           if(res.code === 200){
                  this.$notify.success({
                        title: 'success',
                        duration:1500,
                        message: '保存成功'                     
                        })

                 this.initTemplate(type)  

               }else{
                  this.$notify.error({
                  title: 'error',
                  duration:1500,
                  message: res.msg
               })
               }
         })
      
   },
   //保存为模板
   initTemplate(type){
      const arr=JSON.parse(JSON.stringify(this.list))
      let num=0
      arr.forEach(item =>{
         num+=1
         item.sort=num
      })
      this.form.data=JSON.stringify(arr)
      if(type ==='save' && (this.entryType === 'course' || this.entryType === 'new')){
           const msg=this.entryType === 'course' ? '是否要保存为课程模板?' : '是否要保存为问卷模板?'
           this.$confirm( msg, '提示', {
               confirmButtonText: '是',
               cancelButtonText: '否',
               type: 'warning'
            }).then(() => {
                if(this.entryType === 'course'){
                      this.saveCourseTemplate()
                }else{
                     this.saveTemplate()
                }
               
            }).catch(() => {  
                this.jumpDeal(type)        
            });
      }else{
          this.jumpDeal(type)
      }
      
   },
   //不同跳转处理
   jumpDeal(type){
      let backPath=''
      if(type ==='save'){
         if(this.entryType === 'new') backPath='/evaluationAndFeedback/researchProgress'
         if(this.entryType === 'template') backPath='/evaluationAndFeedback/questionnaireLibrary'  
         if(this.entryType === 'graduate') backPath='/assessmentOfGraduationRequirements/graduationRequirementsQuestionnaire' 
         if(this.entryType === 'course') backPath='/courseQualityEvaluation/courseQualityQuestionnaire'     
      }else{
         backPath='/evaluationAndFeedback/publish'                     
      }
      setTimeout(()=>{
            this.$router.push({
            path: backPath,
            query:{
               id:this.form.formId,
               type:'unSubmit'
            }
         }) 
      },500)
   },
   //保存到普通问卷模板库
   saveTemplate(){
        this.$post(this.api.saveTemplate(), {formId: this.form.formId})
         .then(res => {
           if(res.code === 200){
                  this.$notify.success({
                        title: 'success',
                        duration:1500,
                        message: '保存成功'                     
                        })
                  localStorage.setItem('curIndex', 1)
                  setTimeout(()=>{
                        this.$router.push({
                        path: '/evaluationAndFeedback/questionnaireLibrary'
                     }) 
                  },500)
               }else{
                  this.$notify.error({
                  title: 'error',
                  duration:1500,
                  message: res.msg
               })
               }
         })
   },
   //保存到课程质量模板库
   saveCourseTemplate(){
        this.$post(this.api.saveCourseTemplate(), {formId: this.form.formId})
         .then(res => {
           if(res.code === 200){
                  this.$notify.success({
                        title: 'success',
                        duration:1500,
                        message: '保存成功'                     
                        })
                  localStorage.setItem('curIndex', 2)
                  setTimeout(()=>{
                        this.$router.push({
                        path: '/courseQualityEvaluation/courseLibrary'
                     }) 
                  },500)
               }else{
                  this.$notify.error({
                  title: 'error',
                  duration:1500,
                  message: res.msg
               })
               }
         })
   },
   isEditFn(value){
     const typeArr=['name', 'phone', 'email', 'sex', 'academy', 'specialty', 'industry', 'profession']
     return typeArr.includes(value) ? false : true
   }
 },
 destroyed () {
     Self.$off('OpenSideBar')
 	 window.removeEventListener('scroll', function(e){})
  }
}
</script>

<style lang='scss'>
 @import '../../styles/center.scss';
 @import '../../styles/questionnaire.scss';
 .ghostClass{
    opacity:1
 }
  .el-scrollbar__view{
      .el-select-dropdown__item{
          font-size: 12px;
        }
      .el-select-dropdown__item.selected{
        font-weight: 300;
      }
   }  
</style>



